.menu {
  // border: 1px solid #707070;
  background-color: black;
  height: 80px;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  z-index: 2;
}

.menu__title {
  flex-grow: 1;
  line-height: 21px;
  font-size: 15px;
  text-align: center;
  background-repeat: no-repeat;
  height: 80px;
  background-image: url(#{$baseUrl}asset/image/pma-logo.svg);
  background-position: center center;
}

.menu__disc {
  // background-color: rgba(0, 0, 0, 0.5);
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 0;
  color: white;
  // display: inline-block;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  &[for="menu"] {
    margin-right: 20px;
  }

  &:first-child {
    margin-left: 20px;
  }
}

#menu {
  display: none;
}

#menu + label {
  & .icon__cross {
    display: none;
  }
}

#menu:checked + label {
  & .icon__menu7 {
    display: none;
  }

  & .icon__cross {
    display: block;
  }

  // #menu:checked + label + .menu__list
  & + .menu__list {
    right: 0;
  } 
}

.menu__list {
  background-color: black;
  // border: 1px solid #707070;
  position: fixed;
  bottom: 80px;
  right: -100%;
  transition: 250ms;
  display: flex;
  flex-direction: column;


  a {
    line-height: 40px;
    padding: 0 1.6rem;
    color: white;
    text-decoration: none;
  }
}


a.menu__disc {
  text-decoration: none;
}